.row__loading_container {
  display: flex;
  height: 100%;
  width: 100%;
}

.row {
  margin-left: 20px;
  color: #fff;
  z-index: 0;
  padding-bottom: 30px;
  z-index: 3;
}

.hide-row {
  z-index: 0;
  overflow: visible;
}

.row::-webkit-scrollbar {
  display: none;
}

.row__poster {
  width: 100%;
  height: 100%;
  cursor: pointer;
  border-radius: 4px;
}

.row__poster_add_icon {
  position: absolute;
  height: 24px;
  width: 24px;
  right: 3px;
  top: 4.4px;
  z-index: 2;
  transition: transform 250ms ease-in-out, opacity 100ms ease-in-out;
  color: black;
  opacity: 0.75;
  backface-visibility: hidden;
}

.row__poster_back_img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  cursor: pointer;
  transition: all 250ms ease-in-out;
  border-radius: 4px;
  opacity: 0.25;
  position: absolute;
  top: 0px;
  z-index: -1;
}

.row__poster_container {
  border-radius: 4px;
  width: 100%;
  cursor: pointer;
  position: relative;
  /* FLIP CARD TRANSITION */
  transition: all 250ms;
  z-index: -1;
  max-width: 13rem;
  min-width: 13rem;
  object-fit: cover;
  height: 100%;
  min-height: 340px;
  max-height: 340px;
  transform-style: preserve-3d;
  /* box-shadow: -3px 3px 5px #000; */
  /* box-shadow: 0 2px 9px 3px #000; */
}

.row__poster_container.flip {
  transform: rotateY(0.5turn);
}

.row__poster_front,
.row__poster_back {
  position: absolute;
  z-index: 0;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  border-radius: 4px;
  /* CARD MOVING UP TRANSITION */
  transition: transform 300ms;
}

.row__poster_back {
  transform: rotateY(0.5turn);
  box-shadow: 0 2px 9px 3px #000;
  overflow-y: scroll;
}

.row__poster_back::-webkit-scrollbar {
  display: none;
}

.row__poster_back h3 {
  z-index: 1;
  text-align: center;
  width: 100%;
  font-size: 1.25rem;
  margin-bottom: 5px;
}

.row__poster__esrb_img {
  height: 100%;
  width: 48px;
  max-height: 70px;
  position: absolute;
  bottom: 4px;
  left: 3px;
}

.row__poster_music_icon {
  position: absolute;
  height: 25px;
  width: 25px;
  right: 31px;
  top: 3.5px;
  z-index: 2;
  transition: transform 250ms ease-in-out, opacity 100ms ease-in-out,
    border-radius 250ms ease-in-out, color 250ms ease-in-out;
  color: black;
  opacity: 0.75;
  backface-visibility: hidden;
  border-radius: 0;
}

.row__poster_music_icon:hover {
  color: blue;
}

.row__poster_music_icon:hover,
.row__poster_add_icon:hover {
  opacity: 1;
  cursor: pointer;
  border-radius: 12px;
}

.row__placeholder__wrapper {
  max-width: 12rem;
  min-width: 12rem;
  min-height: 300px;
  max-height: 300px;
  margin-right: 10px;
}

.row__poster_name {
  position: absolute;
  bottom: 5px;
  left: 5px;
  text-transform: uppercase;
  font-weight: 600;
  width: 95%;
  z-index: 1;
  transition: all 400ms;
  backface-visibility: hidden;
}

.row__posters {
  display: flex;
  padding: 15px 15px 40px;
  position: relative;
  overflow-x: scroll;
  overflow-y: hidden;
}

.row__poster_wrapper {
  position: relative;
  transform-style: preserve-3d;
  transition: all 250ms ease-in-out;
  z-index: 3;
  border-radius: 4px;
  margin-right: 25px;
  /* margin-right: 15px; */
  height: 100%;
  width: 100%;
  box-shadow: -2px 2px 9px 3px #000;
}

.row__poster_wrapper:hover {
  box-shadow: -5px 9px 7px 2px #000;
  transform: translate3d(1px, -3px, 0px);
}

.row__poster_wrapper:hover > .row__poster_name {
  transform: translate3d(1px, -3px, 0px);
}

.row__poster_wrapper:hover > .row__poster_music_icon {
  transform: translate3d(1px, -3px, 0px);
}

.row__posters::-webkit-scrollbar {
  display: none;
}

.row__posters_container {
  display: flex;
  width: 100%;
}

.row__title {
  font-size: 2.2rem;
  font-style: italic;
  text-shadow: 4px 4px 2px black;
}

.soundtrack_container {
  width: 100%;
}

.soundtracks {
  list-style: none;
  padding: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0;
}

.soundtrack {
  display: flex;
  margin: 3px 4px 0;
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid white;
  border-radius: 4px;
  padding: 2px 15px 2px 10px;
  justify-content: space-between;
  align-items: center;
  font-size: 0.8rem;
}

.viewing_game {
  /* position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); */
  z-index: 4;
  pointer-events: none;
  height: auto;
  width: auto;
  animation: displayGame 500ms ease-in-out forwards;
}

@keyframes displayGame {
  0% {
    position: relative;
    transform: translate(0, 0) scale(1);
  }
  1% {
    position: fixed;
    bottom: 0;
    left: 0;
  }
  /* 50% {
    transform: translate(-50%, -50%) scale(1.1);
  } */
  100% {
    position: fixed;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1.1);
  }
}

/* Mobile Devices */
@media screen and (min-width: 320px) and (max-width: 500px) {
  .row {
    padding-bottom: 10px;
  }

  .row__title {
    width: 100%;
    white-space: pre;
    font-size: 1.25rem;
  }

  .row__poster_container {
    max-width: 11rem;
    min-width: 11rem;
    min-height: 240px;
    max-height: 240px;
  }

  .row__posters {
    padding: 15px 15px 30px;
  }
}
